
<template>
  <!-- Us del p5js -->
  <div id="vue-canvas"></div>
</template>

<script>
export default {
  name: "VueCanvas",
  mounted() {
    //codi del p5js que es mostrara al div del template
    const script = function(p5) {
      // variables globals
      var cont=0;
      var tamany=0;
      var valor = []; 
      // Setup
      //creem el canvas
      p5.setup = _ => {
        const canvas = p5.createCanvas(1200, 400);
        canvas.parent("vue-canvas");
      };
      // funcio draw
      // cridem les funcions dibuixa i arbre, tambe posem color al fons
      p5.draw = _ => {
        p5.background("#C7E3F7");
        p5.dibuixa();
        p5.arbre();
      }
      // funcio que actua quan es fa clic al ratoli (no hem definit que hagi de ser dins el canvas)
      // guardem la informacio de les posicions en la llista i actualitzem el contador per a que nomes es puguin arribar a mostrar 15 gats a la vegada
      p5.mousePressed = _ => {
        p5.pop();
        tamany = p5.random(0.1,0.3);
        p5.push();
        if (cont < 15)  {
            valor.push(p5.mouseX);
            valor.push(p5.mouseY);
            cont++;
        }
        else {
          cont=0;
          valor.splice(-valor.length);
        }
      }
      //funcio auxiliar que ens serveix per recorre la llista i cridar la funcio que dibuixa els gatets
      p5.dibuixa = _ => {
        for (var i = 0; i < valor.length; i += 2) {
          p5.push();
          p5.translate(valor[i], valor[i + 1]);
          //traslladem el centre a la posicio 'i' i 'i+1' de la llista de estrelles
          p5.cat();
          p5.pop();
        }
      }
      //funcio auxiliar per dibuixar els gats
      p5.cat = _ => {
        p5.scale(tamany);
        p5.stroke(255);
        p5.fill(255);
        p5.triangle(55, 170, 40, 20, 170, 60);
        p5.stroke(0);
        p5.fill(0);
        p5.triangle(230, 60, 360, 20, 345, 170);
        p5.ellipse(200,200,300,300);
        p5.ellipseMode(p5.RADIUS);
        p5.fill(255);
        p5.ellipse(140, 180, 45, 45); 
        p5.ellipseMode(p5.CENTER);
        p5.fill(0,100,180);
        p5.ellipse(140, 180, 75, 75); 
        p5.ellipseMode(p5.RADIUS);
        p5.fill(255);
        p5.ellipse(260, 180, 45, 45); 
        p5.ellipseMode(p5.CENTER);
        p5.fill(0,100,180);
        p5.ellipse(260, 180, 75, 75); 
        p5.stroke(255)
        p5.fill(0)
        p5.triangle(180,220,200,245,220,220);
        p5.line(70,240,0,220);
        p5.line(70,250,0,250);
        p5.line(70,260,0,280);
        p5.line(400,220,330,240);
        p5.line(330,250,400,250);
        p5.line(400,280,330,260);
        p5.line(200,245,180,265);
        p5.line(220,265,200,245);
        }
        // funcio auxiliar per dibuixar l'arbre (en la resolucio mobil no apareix)
        p5.arbre = _ => {
          p5.push();
          p5.translate(400,0);
          p5.scale(0.55);
          p5.noStroke();        //tronc
          p5.fill(70,50,0);
          p5.rect(300,600,120,150);
          p5.stroke(108,71,2);
          p5.stroke(41,167,50);      //arbre
          p5.strokeWeight(1);
          p5.fill(25,229,40);
          p5.triangle(160,600,560,600,350,300);    //baix 
          p5.triangle(200,450,500,450,350,200);    //mig
          p5.triangle(240,300,460,300,350,130);    //alt
          p5.noStroke();            //estrella
          p5.fill(255,226,3);
          p5.beginShape();
            p5.vertex(350,80);
            p5.vertex(340,110);
            p5.vertex(310,115);
            p5.vertex(335,125);
            p5.vertex(325,155);
            p5.vertex(350,135);
            p5.vertex(375,155);
            p5.vertex(365,125);
            p5.vertex(390,115);
            p5.vertex(360,110);
            p5.vertex(350,80);
        p5.endShape();
        p5.strokeWeight(1);       //boles    
        p5.stroke(6,183,146);
        p5.fill(17,240,193);
        p5.ellipse(240,300,30,30);

        p5.stroke(123,3,160);
        p5.fill(216,109,250);
        p5.ellipse(460,300,30,30);

        p5.stroke(188,51,6);
        p5.fill(250,76,18);
        p5.ellipse(200,450,30,30);

        p5.stroke(1,54,142);
        p5.fill(12,100,247);
        p5.ellipse(500,450,30,30);

        p5.stroke(216,2,170);
        p5.fill(255,10,203);
        p5.ellipse(160,600,30,30);

        p5.stroke(198,137,2);
        p5.fill(255,185,33);
        p5.ellipse(560,600,30,30);
        p5.pop();
      }
    };
    // NOTE: Use p5 as an instance mode
    const P5 = require("p5");
    new P5(script);
  }
};
</script>

<style scoped>
#vue-canvas {
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 78%;
  height: 50%;
  border-radius: 20px;
  overflow: hidden;
}
</style>
