<template>
  <div class="center">
  <div class="overflow-auto">
    <!-- mostrem la taula -->
    <b-table aria-label="taula de continguts"
      id="my-table"
      :items="items"
      :per-page="perPage"
      :current-page="currentPage"
      :fields="fields"
      small
    ></b-table>
    <!-- mostrem despres la paginacio, aliniada al centre -->
    <b-pagination aria-label="paginacio de la taula"
      v-model="currentPage" pills 
      :total-rows="rows"
      :per-page="perPage"
      size="md"
      align="center"
      aria-controls="my-table"
    ></b-pagination>
  </div>
</div>
</template>

<script>
  export default {
    data() {
      return {
        //mostrem 5 files de la taula i definim la inicial com la primera
        perPage: 5,
        currentPage: 1,
        //camps que hi ha a la taula, el primer es pot ordenar
        fields:[
          {
            key: 'nom',
            sortable: true
          },
          {
            key: 'edat',
            sortable: false
          }
        ],
        //contingut de la taula
          items: [
            {edat: "8 anys", nom: 'Zeus'},
            {edat: "9 mesos", nom: 'Pillo'},
            {edat: "11 mesos", nom: 'Alex'},
            {edat: "4 anys", nom: 'Charlie'},
            {edat: "12 anys", nom: 'Larry'},
            {edat: "7 mesos", nom: 'Micio'},
            {edat: "2 mesos", nom: 'Boo'},
            {edat: "3 anys", nom: 'Moon'},
            {edat: "2 anys", nom: 'Lev'},
            {edat: "1 any", nom: 'Lu'},
            {edat: "5 mesos", nom: 'Queen'},
            {edat: "9 anys", nom: 'Eve'},
            {edat: " mesos", nom: 'Nars'},
            {edat: "7 anys", nom: 'Titi'},
            {edat: "6 mesos", nom: 'Jami'}
          ]
        }
    },
    computed: {
      rows() {
        return this.items.length
      }
    }
  }
</script>

<style>
/*estil de la paginacio per caviar el color*/
.page-item.active .page-link{
  z-index: 1;
  color: #fff;
  background-color: #C7E3F7!important;
  border-color: #C7E3F7!important;
}
.page-link {
  position: relative;
  display: block;
  padding: 0.5rem 0.75rem;
  margin-left: -1px;
  line-height: 1.25;
  color: #000000!important;
}
/*estils de la taula per a que es mostri com volem*/
.table {
  color: #010101;  
}
.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #909090!important;
  background-color:  #909090!important;
}
.table tr {
  border-bottom: 2px solid #E7E3E4!important;
  background-color:  #C7E3F7!important;
}
.overflow-auto {
  overflow: auto!important;
  margin-top: 1em;
  width: 80%;
}
.btn:not(:disabled):not(.disabled) {
  cursor: pointer;
  color: black;
}
.center {
  display: flex;
  justify-content: center;
}
</style>